<template>
    <div class="chooseCityForAddress" title="选择城市" style="height: 100%;background: #F7F7F7;display: flex;flex-direction: column;">
        <div class="searchBar" :style="{paddingTop:this.$stbHeight+'px',flex:1,overflow:hidden}">
            <van-form action="">
                <van-search
                        v-model="searchText"
                        show-action
                        shape="round"
                        type="search"
                        placeholder="请输入城市名"
                        @search="onSearch"
                        @cancel="onCancel"
                        @click="showSerchView"
                        @clear="onClear"
                        @blur="onClear"
                >
                    <div slot="left-icon">
                        <img class="searchIcon" src="../../assets/images/mall/search_black.png">
                    </div>
                    <template #action>
                        <div
                                class="searchActionBtn"
                                :class="{searchBtn :!searchNull }"
                                @click="searchBtnAction"
                        >{{searchNull ? '取消' : '搜索'}}</div>
                    </template>
                </van-search>
            </van-form>
            <div v-show="!showSearch" style="flex: 1;display: flex;flex-direction: column;overflow: hidden;">
                <div class="currentCityDiv">
                    <div class="firstLine">当前定位</div>
                    <div class="secLine" @click="getCurrentLocation">
                        <div class="leftDiv">
                            <img src="../../assets/images/mall/cityLocation_icon.png" width="14" height="14">
                            <div class="cityDiv">{{currentCityName}}</div>
                        </div>
                        <div class="rightDiv">
                            <img src="../../assets/images/mall/cityLocation_btn.png" width="14" height="14" >
                            <div style="color: #fb5768 " @click="getCurrentLocation">重新定位</div>
                        </div>

                    </div>
                </div>
                <div class="spanDiv">&nbsp</div>
                <div class="citySearch" id="citySearch">
                    <van-index-bar
                            ref="vanInddexBar"
                            v-show="!showSearch"
                            :sticky="false"
                            :index-list="indexList"
                            highlight-color="#FB5768"
                            style="height:100%;overflow:auto;"
                            @select="selectIndexBar"
                    >
                        <div v-for="(item,index) in cities" :key="index" >
                            <van-index-anchor :index="item.firstSpell"/>
                            <van-cell
                                    :title="city.cityName"
                                    v-for="city in item.cityList"
                                    :key="city.cityCode"
                                    @click="selectCityAction(city)"
                                    :class="{listThemeL:bindThemeL}"
                                    :border="hasBorder"
                            />
                        </div>
                    </van-index-bar>
                </div>

            </div>
            <div style="height:100%;overflow: scroll;" v-if="showSearch">
                <van-cell
                        :title="city.cityName"
                        v-for="city in searchResults"
                        :key="city.cityCode"
                        @click="selectCityAction(city)"
                        :class="{listThemeL:bindThemeL}"
                        :border="hasBorder"
                />
            </div>
        </div>
    </div>
</template>

<script>
    import {getAllCityInfo, getCityListNew} from "../../services/MineService";
    import loadMap from "../../components/AMapLoader/loadMap";

    export default {
        name: "choseCityForAddress",
        data(){
            return{
                cities:[],
                selectCity: {},
                searchText: "",
                searchNull: true,
                showSearch: false,
                allCityList:[],
                indexList: [],
                searchResults:[],
                cityName:'',
                /*大字模式*/
                bindThemeL: this.$curTheme==0 ? false : true,
                hasBorder: this.$curTheme==0 ? true : false,
                currentCityName:'',
            };
        },
        mounted(){
            this.getAllCityInfoService();
            this.getCurrentLocation();
            setTimeout(function() {
                document
                    .querySelector("#citySearch")
                    .append(document.querySelector(".van-index-bar__sidebar"));
            }, 100);
        },
        beforeRouteLeave(to, from, next) {
            if ((to.name == "mapForAddress") && Object.keys(this.selectCity).length != 0) {
                console.log(this.selectCity)
                to.query.currentCity = this.selectCity.cityName;
                // this.$emit("city",this.selectCity.cityName);
            }
            next();
        },
        watch: {
            searchText() {
                if (this.searchText !== "") {
                    this.searchNull = false;
                } else {
                    this.searchNull = true;
                }
            }
        },
        methods:{
            selectIndexBar(index){
                this.$nextTick(()=>{
                    this.$refs.vanInddexBar.$el.scrollTop += 1;
                    console.log()
                })
            },
            onSearch() {
                this.searchResults = []; //修复搜索小区结果不清空问题
                for (let item of this.allCityList) {
                    if (item.cityName.match(this.searchText)) {
                        this.searchResults.push(item);
                    }
                }
                console.log(this.searchResults);
                if (this.searchResults.length == 0) {
                    this.$toast("没有搜索到该城市");
                }
            },
            onCancel() {
                this.$router.back(-1);
            },
            showSerchView() {
                this.showSearch = true;
            },
            onClear(e){
                console.log("=============>"+e)
                this.showSearch = false;
            },
            getAllCityInfoService(){
                getAllCityInfo().then((result)=>{
                    console.log(result);
                    this.cities = result.data;
                    result.data.forEach((item)=>{
                        this.indexList.push(item.firstSpell);
                        this.allCityList=this.allCityList.concat(item.cityList)
                    })
                }).catch((err)=>{
                    console.log(err);
                })
            },
            searchBtnAction() {
                if (!this.searchNull) {
                    this.onSearch();
                } else {
                    this.onCancel();
                }
            },
            selectCityAction(city) {
                console.log(city);
                this.selectCity = city;
                this.$router.back(-1);
            },
            getCurrentLocation() {
                let that = this;
                loadMap()
                    .then(AMap => {
                        AMap.plugin('AMap.Geolocation', function () {
                            var geolocation = new AMap.Geolocation({
                                enableHighAccuracy: true,
                                // 设置定位超时时间，默认：无穷大
                                timeout: 10000
                            });
                            geolocation.getCurrentPosition();
                            AMap.event.addListener(geolocation, 'complete', onComplete);
                            AMap.event.addListener(geolocation, 'error', onError);
                            function onComplete (data) {
                                console.log(data)
                                // data是具体的定位信息
                                if (data.addressComponent){
                                    that.currentCityName = data.addressComponent.city==""?data.addressComponent.province:data.addressComponent.city;
                                }
                                //定位城市非青岛市，需提示用户
                                console.log("定位结果："+that.currentCityName)
                            }
                            function onError (data) {
                                // 定位出错
                                console.log('定位失败错误：', JSON.stringify(data));
                            }
                        })
                    })
                    .catch((err) => {
                        console.log('地图加载失败！'+err);
                    });
            },
        },
    }
</script>

<style scoped lang="scss">
    .searchBar{
        /*width: 300px;*/
        /*height: 38px;*/
        /*!*padding: 7px 15px;*!*/
        /*background: #F7F7F7;*/
        /*border-radius: 19px;*/
        /*line-height: 38px;*/
        /*margin-top: 7px;*/
        /*margin-left: 15px;*/
        display: flex;
        flex-direction: column;
        flex: 1;
        overflow: hidden;
    }
    .currentCityDiv{
        height: 87px;
        background: #FFFFFF;
        .firstLine{
            padding-top: 15px;
            margin-left: 15px;
            font-family: PingFangSC-Regular;
            font-size: 14px;
            color: #9A9A9A;
            letter-spacing: 0;
            text-align: left;
        }
        .secLine{
            margin-top: 15px;
            margin-bottom: 15px;
            display: flex;
            justify-content: space-between;
            .leftDiv{
                margin-left: 15px;
                display: flex;
                .cityDiv{
                    margin-left: 10px;
                    font-family: PingFangSC-Regular;
                    font-size: 16px;
                    color: #464646;
                    letter-spacing: 0.13px;
                    text-align: center;
                    align-items: center;
                }
            }
            .rightDiv{
                display: flex;
                align-items: center;
                font-family: PingFangSC-Regular;
                font-size: 14px;
                color: #FB5768;
                letter-spacing: 0;
                text-align: left;
                margin-right: 15px;
            }
        }
    }
    .searchActionBtn {
        padding: 0 10px;
        font-size: 14px;
        color: #fb5768;
        line-height: 34px;
        height: 34px;
    }
    .currentLocation{
        height: 44px;
        line-height: 44px;
    }
    .searchBtn {
        color: #fb5768;
    }
    .spanDiv{
        height: 10px;
    }
    .citySearch{
        background: #FFFFFF;
        overflow: hidden;
        flex: 1;
    }
    /*自定义搜索图标*/
    .searchIcon {
        width: auto;
        height: 13px;
        margin-left: 3px;
        margin-right: 2px;
    }
    /deep/.van-index-bar > div:last-child {
        min-height: 100%;
    }
    /deep/.van-index-bar__sidebar {
        /*width: 24px;*/
        margin-top: 75px;
    }
    /deep/.van-index-bar__index {
        color: #464646;
        height: 20px;
        line-height: 20px;
        width: 24px;
        font-family: PingFangSC-Regular;
        font-size: 14px;
        letter-spacing: 0;
        text-align: center;
    }
</style>